import Request from './request'
import {refreshToken} from '@/api/login';
import indexConfig from '@/config/index.config';
import mHelper from '@/utils/helper';
import mCrypto from '@/utils/crypto.js';
import store from '@/store';

const http = new Request();

// request
http.setConfig((config) => { /*  */
	config.baseUrl = indexConfig.baseUrl; /*  */
	const systemInfo = uni.getSystemInfoSync();
	const systemInfoHeaders = {
		'device-name': systemInfo.brand, // 
		'width': systemInfo.screenWidth, // 
		'height': systemInfo.screenHeight, // 
		'os': systemInfo.platform, // 
		'os-version': systemInfo.system // 
	};
	config.header = {
		...config.header,
		...systemInfoHeaders
	};
	return config;
});

let isRefreshing = false;
let requests = [];
http.interceptor.request((config) => { /*  */
	config.header['x-api-key'] = uni.getStorageSync('accessToken');
	// 
	// config.header['merchant-id'] = uni.getStorageSync('merchantId') || 1;
	return config;
}, error => {
	return Promise.reject(error)
})

// refreshToken
async function handleRefreshToken(refresh_token) {
	const params = {};







	params.group = 'tinyShopWechatMq';




	params.refresh_token = refresh_token;
	await http.post(refreshToken, params).then(async r => {
		store.commit('login', r.data);
		isRefreshing = false;
	});
}
http.interceptor.response(async (response) => { /*  */
	switch (response.data.code) {
		case 200:
			return response.data;
		case 400:
			mHelper.toast('');
			return Promise.reject(response.data.message);
			break;
		case 401:
			isRefreshing = false;
			// refreshToken 401
			if (response.config.url === refreshToken) {
				mHelper.backToLogin();
				throw response.data.message;
				break;
			} else {
				// refreshToken 
				if (!store.state.refreshToken) {
					mHelper.backToLogin();
					throw response.data.message;
				} else {
					// isRefreshing
					if (!isRefreshing) {
						isRefreshing = true;
						// token
						await handleRefreshToken(store.state.refreshToken, response);
						requests.forEach(cb => cb());
						requests = [];
						isRefreshing = false
						return http.request(response.config);
					} else {
						return new Promise((resolve) => {
							// resolvetoken
							requests.push(() => {
								resolve(http.request(response.config));
							})
						})
					}
				}
			}
			break;
		case 405:
			mHelper.toast('');
			return Promise.reject(response.data.message);
		case 404:
			mHelper.toast(response.data.message);
			return Promise.reject(response.data.message);
		case 429:
			mHelper.toast('');
			return Promise.reject(response.data.message);
		case 500:
			mHelper.toast('');
			return Promise.reject(response.data.message);
		default:
			mHelper.toast(response.data.message);
			return Promise.reject(response.data.message);
	}
}, (error) => {
	return Promise.reject(error)
});

export {
	http
}
